<template>
  <div class="w-batch">
  	<h3>
  		<span></span>
  		<a href="/batchProcess"></a>
  	</h3>
  	<div class="step">
  		<h4></h4>
  		<p>
  			<span>1</span><a href="javascript:;"></a>
  		</p>
  		<p>
  			<a :href="exportGoodsList">
  				<button></button>
  			</a>
  		</p>

  		<p>
  			<span>2</span><a href="javascript:;"></a>
  		</p>
  		<p>
  			<a :href="exportOrderTem">
  				<button></button>
  			</a>
  		</p>

  		<p>
  			<span>3</span>
  		</p>
  		<p>
  			--
  		</p>

		<el-upload
			class="upload"
			accept="excel"
			:data="{'name':'excel'}"
			:action="loadUrl"
			:on-change="handleSuccess"
			:on-success="success"
			:on-error="backError"
			:file-list="fileList"
			:on-preview="loadSuccess"
			:before-upload="beforeUpload"
		>
			<el-button size="small" type="primary"></el-button>
		</el-upload>

		<p>
	  		<i>{{loadResponse.successNum}}</i>
	  		<i>{{loadResponse.failNum}}</i>
  		</p>

  		<p v-if="loadResponse.failNum > 0">
  			<a class="load-failed" :href="loadFailedUrl" @click="loadfail"></a>
  		</p>

	
  	</div>
  </div>
</template>

<script>
export default {
	name: 'batch',
	data () {
		return {
			fileList:[],
			loadUrl:this.$api.batch.importOrders,				// 
			loadFailedUrl:this.$api.batch.getFailExcel,			// 
			exportGoodsList:this.$api.batch.exportGoodsList,	// 
			exportOrderTem:this.$api.batch.exportOrderTem,		// 
			loadResponse:this.$store.state.fileLoadNum,
			fileName:[]
		}
	},
	beforeCreate(){

	    let proof  = this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');

	    if(proof == 1 ){
	    	// 
        }else if(proof == -1){            // 

        	this.$anchu_message.warn('');
        	this.$router.push('/');

        }else if(proof == -2 || proof == -3 ){     //  

        	this.$anchu_message.error('');
            this.$router.push('/proof');

        }else if(proof == -4 ){     // 

        	this.$anchu_message.warn('');
            this.$router.push('/proof');

    	}else{

        	this.$router.push('/login');
        }

	},
	mounted(){
		$('body,html').animate({scrollTop:0},10);
	},
	methods:{

		// 
		loadfail(){
			this.$store.commit('setFileLoad',{
				failed:0,
            	success:0
			});
		},

	  	// 
	  	beforeUpload(file){

	  		const fileSuffix = file.name.slice(file.name.lastIndexOf('.')+1)

	  		if(this.fileName.length >= 1){
	  			this.$anchu_message.error('');
	  			return false;
	  		}

	  		if(fileSuffix.indexOf('xl') == 0){

	  			this.fileName.push(file.name);

	  		}else{
	  			this.$anchu_message.error('');
	  			this.fileList = [];
	  			return false;
	  		}

	  	},

	  	//  
	  	loadSuccess(file){
	  		if(file.status =="fail"){
	  			this.$anchu_message.error('');
	  			return false;
	  		}else if(file.status =="ready"){
	  			return false;
	  		}

	  		if(file.response.error == 0){
	  			//  vuex
	  			this.$store.commit('setFileLoad',file.response.data);
	  		}else{
	  			this.$anchu_message.error(file.response.error_msg);
	  			this.fileList=[];
	  			this.fileName=[];
	  			
	  		}
	  	},

	  	//   
	  	handleSuccess(file, fileList) {
	  		// console.log(this.loadUrl)
	  		this.$nextTick(function(){

                document.getElementsByClassName('el-upload-list__item-name')[0].click();

            })
			

	    },
	    success(){
	    	// console.log('success')
	    },
	    backError(){
	    	this.$anchu_message.error('');
	    }
  }
}
</script>

<style scoped>
	/* 2.0  2017/06/21 */

	/* 1.*  */
	.w-batch{
		width:1018px;
		float: left;
		padding:0 0 130px 41px;
		background-color:#fff;
		min-height: 500px;
	}
	.w-batch button{
		cursor:pointer;
		font-size:14px;
	}
	.w-batch h3{
		color:#333;
		text-align: left;
		padding-bottom:15px;
		font-size: 16px;
		border-bottom: 1px solid #D8D8D8;
		margin-bottom: 22px;
		font-weight: 900;
	}
	.w-batch h3 a{
		margin-left: 10px;
		color: #009e4d;
		font-size: 14px;
		font-weight: 100;
	}

	.w-batch h4,.w-batch p{
		text-align: left;
		font-size:14px;
		color:#333;
	}
	.w-batch h4{
		margin-bottom:26px;
	}
	.w-batch p span{
		display: inline-block;
		width:19px;
		height:19px;
		color:#fff;
		border-radius:4px;
		background-color: #009E4D;
		text-align: center;
		font-size:14px;
		line-height:19px;
		margin:0 15px;
	}
	.w-batch p a{
		color:#E64346;
		cursor: pointer;
	}
	.w-batch p button{
		width:78px;
		height:28px;
		border:1px solid #D8D8D8;
		background-color: #F2F2F2;
		margin-left:48px;
	}
	.w-batch .step p:nth-of-type(2),.w-batch .step p:nth-of-type(4){
		margin:12px 0 24px;
	}
	.w-batch .step p:nth-of-type(6){
		margin:44px 0 16px;
		box-sizing: border-box;
		padding-left:50px;
		color:#999;
	}
	.w-batch .upload{
		margin-left: 50px;
		display: inline-block;
	}
	.w-batch .step p:nth-of-type(7){
		color:#E64346;
		margin-top:14px;
		box-sizing: border-box;
		padding-left: 15px;
	}
	.w-batch .step p:nth-of-type(7) i{
		color:#E64346;
		margin-right: 20px;
	}
	.w-batch .step p:last-of-type{
		color:#E64346;
		margin-top:14px;
		box-sizing: border-box;
		padding-left: 50px;
	}
	.w-batch .step p:last-of-type a{
		color:#E64346;
		margin-right: 20px;
		text-decoration:underline;
	}
</style>


