<template>
    <div class="certification">
    
        <div class="address-header"><span class="certfy">{{proofInfo}}</span></div>

        <!--  -->
        <div v-if="proofStatus == 1 ||proofStatus == -1" v-show="code != -1">
            <div class="operating-area">
 
                <el-row class="items">
                    <el-col :span="6" class="name"></el-col>
                    <el-col :span="18" class="value">
                        <div class="value-bg" v-text="certification.authentication_type == 1?'':''"></div>
                    </el-col>
                </el-row>

                <el-row class="items">
                    <el-col :span="6" class="name"></el-col>
                    <el-col :span="18" class="value">
                        <div 
                            class="value-bg" 
                            v-text="manageType(certification.authentication_type,certification.management_type)"
                        >
                        </div>
                    </el-col>
                </el-row>

                <el-row class="items">
                    <el-col 
                        :span="6" 
                        class="name" 
                        v-text="certification.authentication_type ==1 ? '' : ''"
                    >
                    </el-col>
                    <el-col :span="18" class="value">
                        <div 
                            class="value-bg" 
                            v-text="certification.merchant_name"
                        >
                        </div>
                    </el-col>
                </el-row>

                <el-row class="items">
                    <el-col 
                        :span="6" 
                        class="name" 
                        v-text="certification.authentication_type ==1 ? '' : ''"
                    >
                    </el-col>
                    <el-col :span="18" class="value">
                        <div 
                            class="value-bg" 
                            v-text="certification.website"
                        >
                        </div>
                    </el-col>
                </el-row>

                <el-row class="items">
                    <el-col :span="6" class="name" ></el-col>
                    <el-col :span="18" class="value">
                        <div class="value-bg" v-text="certification.contacts_name"></div>
                    </el-col>
                </el-row>

                <el-row class="items">
                    <el-col :span="6" class="name" ></el-col>
                    <el-col :span="18" class="value">
                        <div class="value-bg" v-text="certification.contacts_mobile"></div>
                    </el-col>
                </el-row>

                <el-row class="items">
                    <el-col :span="6" class="name"></el-col>
                    <el-col :span="18" class="value">
                        <div v-if="photo.length ==1 && photo[0] == '' " class="value-bg"></div>
                        <img 
                            v-for="(item,index) in photo" 
                            v-if="item != ''" 
                            :src="imgPre + item" 
                            :onerror="imgErr" 
                            class="value-img" 
                        />
                    </el-col>
                </el-row>

            </div>
        </div>

        <!--  -->
        <div v-if="proofStatus == -4 || proofStatus == -3 || code == -1" class="verify-fail-wrap">
            <p>
                <img src="/static/images/kongb.png" />
            </p>
            <p style="padding-top:20px;">
                <span>  </span>
                <a href="/proof"></a>
            </p>
        </div>

        <!--  -->
        <div v-if="proofStatus == -2" class="audit-failure-wrap">
            <p class="audit-img">
                <img src="/static/images/certificationFail.png" />
            </p>
            <p class="audit-failtext"></p>
            <p class="audit-again"></p>
            <p class="audit-phone">0571-28270936</p>
            <p class="audit-btn">
                <router-link to="/proof" tag="button"></router-link>
            </p>
        </div>


    </div>
</template>

<script>
  export default {
    name: 'certification',
    data () {
        return {
            imgPre:this.$api.imgPre,
            imgErr:'this.src="/static/images/imgtem.png"',
            proofInfo:'',
            proofStatus:this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299'),
            certification:[],
            photo:[],
            code:-2,

        }
    },
    created(){
        if(this.proofStatus == 1){
            this.proofInfo = '';
        }else if(this.proofStatus == -1){
            this.proofInfo = '';
        }else if(this.proofStatus == -2){
            this.proofInfo = '';
        }else if(this.proofStatus == -3){
            this.proofInfo = '';
        }else if(this.proofStatus == -4){
            this.proofInfo = '';
        }else{
            this.$router.push('/login');
        }
    },
  mounted(){
    this.getMsg()
  },
    methods:{

        // 
        manageType(type,val){
            if(type == 1){
                
                switch(val){
                    case 1: return '';
                    case 2: return '';
                    case 3: return '';
                    case 4: return '';
                    case 5: return '';
                    case 8: return '';
                    case 13: return '';
                    case 14: return '';
                    case 15: return '';
                    case 17: return '';
                    case 18: return '';
                    default: return '';
                }

            }else{

                switch(val){
                    case 1: return '';
                    case 2: return '';
                    case 3: return '';
                    default: return '';
                }

            }
        },

    getMsg(){
      let _this = this;
      $.ajax({
        url: this.$api.user.getUserInfo,
        data:{
          user_id:_this.$cookie.getCookie('a34ea2d96d4239d4d86803bb3cd99788')
        },
        dataType: "json",
        type: 'get',
        success: function (res) {
            if(res.code == -1){
                _this.code = res.code;
                return 
            }
            _this.certification = res.result;
            _this.photo = res.result.photo.split(',');
        },
        error:function (res) {
            _this.$anchu_message.error('');
          }
        });
    }
  }
}
</script>

<style scoped>
  .certification{
    min-height: 380px;
    background: #fff;
    float: left;
    position: relative;
    width: 1017px;
    padding: 0 0 98px 41px;
  }
  .certification .address-header{
    text-align: left;
    border-bottom: 1px solid #D8D8D8; 
    padding-bottom: 12px;
    font-weight: 900;
  }
  .certification .certfy{
    color: #009E4D;
  }
  .certification .operating-area{
    width: 600px;
    margin: 0 auto;
    font-size: 14px;
    
  }
  .certification .items{
    min-height: 35px;
    padding: 10px 0;
  }
  .certification .items:nth-of-type(1) {
    padding: 40px 0 14px 0;
  }
  .certification .items:nth-of-type(1) .value-bg{
    background: #fff;
    border:1px solid #fff;
    font-size: 14px;
    color: #333;
  } 
  .certification .name{
    text-align: right;
    min-height: 30px;
    line-height: 30px;
    color: #999;
  }
  .certification .value{
    text-align: left;
    padding: 0 10px;
    width: 420px;
    color: #666; 
  }
  .certification .value .value-bg{
    min-height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border:1px solid #d8d8d8;
    background: #f2f2f2;
  }
  .certification .value-img{
    width: 100px;
    height: 100px;
    margin-right: 20px;
  }
  .verify-fail-wrap{
    text-align: center;
    padding:50px 0 30px;
    font-size: 14px;
  }
  .verify-fail-wrap p a{
    text-decoration: underline;
    color: #009e4d;
  }
  .verify-fail-wrap p img{
    width: 110px;
    height:auto;
  }
  .audit-failure-wrap{
    text-align: center;
    padding:90px 0 30px;
    font-size: 16px;
  }
  .audit-failure-wrap .audit-failtext{
    margin-top: 48px;
    font-size: 20px;
    color: #009e4d;
  }
  .audit-failure-wrap .audit-again{
    color: #333;
    margin-top: 20px;
  }
  .audit-failure-wrap .audit-phone{
    color: #999;
    margin-top: 14px;
  }
  .audit-failure-wrap .audit-btn button{
    width: 200px;
    height: 42px;
    margin-top: 36px;
    font-size: 20px;
    background: #009E4D;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
  }
</style>


