<template>
	<div class="w-refund">
		<h3></h3>
		<div v-if="error==0">
			<div class="refundInfo">
				
				<div>
					<p>{{status}}</p>
					<p>{{refundInfo.type==0?'':''}}</p>
					<p>{{addTime}}</p>
					<p>{{refundInfo.num}}</p>
					<p>{{refundInfo.price}}</p>
					<p>{{refundInfo.reason}}</p>
					<p v-if="refundInfo.type!=0">
						{{refundInfo.return_address}}
					</p>
					<p>{{refundInfo.comment}}</p>
					<p v-if="refundInfo.status==11">
						{{refundInfo.refuse_reason}}
					</p>
					<p v-if="refundInfo.type==1&&imgList.length!=0">
						<span></span>
						<img v-for="(items,indexs) in imgList" :src="imgPre + items"  key="indexs" />
					</p>
				</div>	
				
			</div>
			<div class="operate" v-if="refundInfo.status==2&&refundInfo.type==1">
				<h3></h3>
				<p>
					
					<input 
					type="text" 
					id="ltType" 
					:disabled="inputDisabled" 
					placeholder="/" />
				</p>
				<p>
					
					<input 
					type="text" 
					id="ltCode" 
					:disabled="inputDisabled"
					placeholder="/" />
				</p>
				<p v-if="refundInfo.delivery_name==''"><i class="iconfont">&#xe60a;</i><span>{{outTime}}</span></p>
				<button @click="postltInfo" class="pointer" v-if="refundInfo.delivery_name==''"></button>
				<p style="color:#FF0000" v-if="refundInfo.delivery_name!=''"></p>
			</div>
		</div>

		<section class="noOrder" v-if="error != 0">
	  		<p><img src="/static/images/kongb.png" /></p>
	  		<p>~</p>
	  	</section>
	</div>
</template>

<script>
export default{
	name:"refundDetail",
	data () {
		return{
			imgPre:this.$api.imgPre,
			ids:"",
			refundInfo:"",
			status:"",
			addTime:"",
			imgList:[],
			outTime:"",
			error:'',
			inputDisabled:false,		// 
		}
	},
	beforeCreate(){
		let proof  = this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');

        if(proof == 1 ){

        }else if(proof == -1){            // 

        	this.$anchu_message.warn('');
        	this.$router.push('/');

        }else if(proof == -2 || proof == -3 ){     //  

        	this.$anchu_message.error('');
            this.$router.push('/proof');

        }else if(proof == -4 ){     // 

        	this.$anchu_message.warn('');
            this.$router.push('/proof');

        }else{
        	this.$router.push('/login');
        }
	},
	created(){
		this.ids = this.$route.query
	},
	mounted(){
		this.getHttp();
	},
	methods:{
		//
		getHttp(){
			let that = this;
			$.ajax({
				type:"get",
				url:this.$api.order.get_return_detail,
				data:that.ids,
				dataType:"json",
				success:function(data){
					console.log(data)
					that.error = data.error;
					let d = new Date();
  					d.setTime(data.data.addtime*1000);
  					that.addTime = d.getFullYear()+""+(d.getMonth()+1)+""+d.getDate()+" " +(d.getHours()>9?d.getHours():"0"+d.getHours())+":"+(d.getMinutes()>9?d.getMinutes():'0'+d.getMinutes())+":"+(d.getSeconds()>9?d.getSeconds():"0"+d.getSeconds());

					that.refundInfo = data.data;

					that.refundInfo.delivery_name!='' ? that.inputDisabled = true : that.inputDisabled = false;

					if(data.data.imgs!=""){
						if(/,/.test(data.data.imgs)){
							that.imgList = data.data.imgs.split(",")
						}else{
							that.imgList = data.data.imgs.split()
						}
					}
					that.timeStamp(data.data.return_remain_time)
					setTimeout(function(){
						$("#ltType").val(data.data.delivery_name)
						$("#ltCode").val(data.data.delivery_no)
					},300)
					//
					switch (that.refundInfo.status){
						case '1':
							that.status = "";
							break;
						case '2':
							that.status="";
							break;
						case '3':
							that.status = "";
							break;
						case '10':
							that.refundInfo.type==0?that.status="":that.status="";
							break;
						case '11':
							that.refundInfo.type==0?that.status="":that.status="";
							break;
						case '12':
							that.status = "";
							break;
					}
				}
			});
		},
		//
	  	timeStamp(second_time){
	  		let that = this;
	  		let SecondTime = setInterval(function(){
				let time = parseInt(second_time) + "";
				if (parseInt(second_time) > 60) {
					let second = parseInt(second_time) % 60;
					let min = parseInt(second_time / 60);
					time = min + "" + second + "";
					if (min > 60) {
						min = parseInt(second_time / 60) % 60;
						let hour = parseInt(parseInt(second_time / 60) / 60);
						time = hour + "" + min + "" + second + "";
						if (hour > 24) {
							hour = parseInt(parseInt(second_time / 60) / 60) % 24;
							let day = parseInt(parseInt(parseInt(second_time / 60) / 60) / 24);
							time = day + "" + hour + "" + min + "" + second + "";
						}
					}
				}
				
				second_time--;


				//  
				if(second_time <= 0){
					clearInterval(SecondTime);
				}

				//    
				if (that && that._isDestroyed){
					clearInterval(SecondTime);
				}

				that.outTime = time;


			},1000)
	  	},

		//
		postltInfo(){
			let that = this;
			let lttype = $("#ltType").val();
			let ltcode = $("#ltCode").val();
			if(/^\d+$/.test(ltcode) && lttype!=""){
				$.ajax({
					type:"post",
					url:this.$api.order.return_delivery,
					data:{
						order_sn:that.refundInfo.order_sn,
						delivery_name: lttype,
						delivery_no: ltcode,
						goods_id:that.ids.goods_id
					},
					dataType:"json",
					success:function(data){
						if(data.error == 0){
							
							that.$anchu_message.success('');
							that.inputDisabled = true;
							that.getHttp();

						}else{
							that.$anchu_message.error(data.error_code);
						}
					}
				});
			}else{
				that.$anchu_message.warn('');
			}
		}
	}
}
</script>

<style scoped>
	.noOrder{
		text-align: center;
		padding: 100px 0 20px;
	}
	.noOrder img{
		width: 110px;
		height:auto;
		margin-bottom: 20px;
	}
	.w-refund{
		float:left;
		background-color:#fff;
		width:970px;
		padding:16px 27px 100px 23px;
	}
	.w-refund h3{
		color:#333;
		font-size:16px;
		padding-bottom:14px;
		border-bottom: 2px solid #D8D8D8;
	}
	.w-refund .refundInfo div{
		margin-top:20px;
		width:905px;
		padding:35px 20px 30px 43px;
		border:1px solid #D8D8D8;
		background-color: #FEFDF9;
		font-size:14px;
	}
	.w-refund .refundInfo div p{
		margin-bottom:6px;
		overflow:hidden;
	}
	.w-refund .refundInfo div p:first-child{
		font-weight: 800;
	}
	.w-refund .refundInfo div p span{
		float:left;
	}
	.w-refund .refundInfo div img{
		width:68px;height:68px;
		border:1px solid #D8D8D8;
		margin-right:15px;
	}
	.w-refund .operate{
		margin-top:60px;
	}
	.w-refund .operate h3{
		margin-bottom: 27px;
	}
	.w-refund .operate p{
		display: flex;
		align-items: center;
		font-size:14px;
	}
	.w-refund .operate p input{
		width:338px;height:28px;
		border:1px solid #D8D8D8;
		padding-left:10px;
	}
	.w-refund .operate p:first-of-type{
		margin-bottom: 20px;
	}
	.w-refund .operate p:first-of-type input{
		border-color:#FF1B1B
	}
	.w-refund .operate p:nth-of-type(3){
		margin-top:6px;
		color:#999;
	}
	.w-refund .operate p:nth-of-type(3) i{
		font-size:14px;
		margin:0 3px 0 70px;
	}
	.w-refund .operate p:nth-of-type(3) span{
		color:#FF1B1B;
	}
	.w-refund .operate button{
		width:88px;height:28px;
		background-color:#009E4D;
		border-radius:4px;
		color:#fff;
		margin:40px 0 0 72px;
	}
</style>


