<template>
    <section class="index-footer">

        <div class="footerImg">
            <img src='/static/images/footer-img.png' />
        </div>

        <div class="footerLink">
            <dl class="index-footer-logo">
                <dd>{{contact.contactWay}}</dd>
                <dd>{{contact.contactTime}}</dd>
                <dt>
                    <p class="pointer" @click="service">
                        <i class="iconfont icon-xinxi"></i>
                        <span></span>   
                    </p>
                </dt>
            </dl>

            <dl 
                class="index-footer-link" 
                v-for="(item,index) in footerLink" 
                key="index"
            >
                <dt>{{item.title}}</dt>
                <router-link 
                    tag="dd" 
                    v-for="(contents,index) in item.content" 
                    key="index" 
                    v-text="contents.subheading"
                    :to="contents.link"
                >
                </router-link>
            </dl>
        </div>

        <div class="footerInfo" v-text="footerInfo"></div>

    </section>
</template>

<script>
export default {
    name: 'index-footer',
    data () {
        return {
            footerInfo:'',
            contact:{
                contactWay:'0571-28270936',
                contactTime:'9:00-18:00',   
            },
            footerLink:[
                {
                    title:'',
                    content:[
                        {
                            subheading:'',
                            link:'/help/origin'
                        },
                        {
                            subheading:'',
                            link:'/help/quality'
                        },
                        {
                            subheading:'',
                            link:'/help/delivery'
                        },
                        {
                            subheading:'',
                            link:'/help/bestservice'
                        }
                    ]
                },
                {
                    title:'',
                    content:[
                        {
                            subheading:'',
                            link:'/help/registercertify'
                        },
                        {
                            subheading:'',
                            link:'/help/userrights'
                        },
                        {
                            subheading:'',
                            link:'/help/platformrules'
                        },
                        {
                            subheading:'',
                            link:'/help/termsofservice'
                        }
                    ]
                },
                {
                    title:'',
                    content:[
                        {
                            subheading:'',
                            link:'/help/pledge'
                        },
                        {
                            subheading:'',
                            link:'/help/pledge'
                        },
                        {
                            subheading:'',
                            link:'/help/returnprocess'
                        },
                        {
                            subheading:'',
                            link:'/help/invoice'
                        },
                        {
                            subheading:'',
                            link:'/help/problem'
                        }
                    ]
                },
                {
                    title:'',
                    content:[
                        {
                            subheading:'',
                            link:'/help/weare'
                        },
                        {
                            subheading:'',
                            link:'/help/contactus'
                        }
                    ]
                }
            ],
            userInfo:{
                name:'',
                address:'',
                mobile:'',
                orderNum:'',
                orderSta:'',
                orderTim:''
            }
        }
    },
    mounted(){
        let date = new Date,
            year = date.getFullYear(); 
        this.footerInfo = `${year}   ICP13026319-1`;

        this.getUserInfo();
    },
    methods:{
        getUserInfo(){
            const _this  = this;
            // 
            $.ajax({
                type: "get",
                url: this.$api.service.getLastOrderByUser,
                dataType: "json",
                success:function(res){

                    if(res.data.mobile){

                        _this.userInfo.name = 'PC-';
                        _this.userInfo.mobile = res.data.mobile;
                        _this.userInfo.address = res.data.address;
                        _this.userInfo.orderNum = res.data.order_sn;
                        _this.userInfo.orderSta = _this.orderStatus(res.data.order_status);
                        _this.userInfo.orderTim = _this.orderTime(res.data.add_time);
                        
                    }else{

                        _this.userInfo.name = 'PC-';
                        _this.userInfo.mobile = '';
                        _this.userInfo.address = '';
                        _this.userInfo.orderNum = '';
                        _this.userInfo.orderSta = '';
                        _this.userInfo.orderTim = '';

                    }

                    // 11  
                    const numSign = parseInt(Math.random()*(99999999999-10000000000))+10000000000;

                    //   
                    try{ 
                        ysf.config({
                            uid:_this.userInfo.mobile > 0 ? _this.userInfo.mobile : numSign,
                            data:JSON.stringify([
                                {"key":"real_name", "value":_this.userInfo.name},
                                {"key":"mobile_phone", "value":_this.userInfo.mobile},
                                {"key":"email", "hidden":true},
                                {"index":2, "key":"account", "label":"","value":_this.userInfo.address},
                                {"index":5, "key":"account", "label":"", "value":_this.userInfo.orderNum},
                                {"index":6, "key":"account", "label":"()", "value":_this.userInfo.orderTim},
                                {"index":7, "key":"account", "label":"()", "value":_this.userInfo.orderSta}
                            ])
                        });
                        
                    }catch (e) { 
                        // console.log('catch')
                    } 

                }
            });
        },

        // 
        service(){
            ysf.open();
        },

        // 
        orderTime(value){
            const d = new Date();
            d.setTime(value*1000);
            return d.getFullYear()+""+(+d.getMonth()+1)+""+d.getDate()+""
        },
        
        // 
        orderStatus(value){
            switch(value){
                case '0': return '';
                case '1': return '';
                case '2': return '';
                case '10': return '';
                default: return '';
            }
        }
    }
}

</script>
<style scoped>
    .index-footer{
        font-size:14px;
        background:#fff;
        overflow: hidden;        
    }
    .footerImg{
        height: 44px;
        background:#fff;
        background-size: 50%;
        padding: 20px 0;
        text-align: center;
        width: 1200px;
        margin: 0 auto;
        border-bottom: 1px solid #efefef;
    }
    .footerImg img{
        display: inline-block;
        height: 100%;
        width: auto;

    }
    .footerLink{
        width: 1200px;
        margin: 0 auto;
        overflow: hidden;
        padding: 30px 0;
    }
    .footerLink dl{
        width: 20%;
    }
    .index-footer-logo{
        float: left;
        padding-left: 60px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        -ms-box-sizing: border-box;
        box-sizing: border-box;

    }
    .index-footer-logo dt{
        margin-top: 10px;
        color: #009e4d;
        text-align: center;
    }
    .index-footer-logo dt p{
        display: inline-block;
        border: 1px solid #009e4d;
        padding: 6px 10px;
    }
    .index-footer-logo dt p span{
        margin-left: 6px;
    }
    .index-footer-logo dd:nth-child(1){
        color: #8a8a8a;
        line-height: 30px;
        font-size: 24px;
    }
    .index-footer-logo dd:nth-child(2){
        color: #8a8a8a;
        font-size: 14px;
        margin: 2px 0 8px;
        text-indent: 13px;
    }
    .index-footer-link{
        float: left;
        padding-left: 100px;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -o-box-sizing: border-box;
        -ms-box-sizing: border-box;
        box-sizing: border-box;
    }
    .index-footer-link dt{
        line-height: 22px;
        font-weight: 900;
        font-size: 14px;
    }
    .index-footer-link dd{
        font-size: 12px;
        line-height: 26px;
        cursor: pointer;
    }
    .index-footer-link dd:hover{
        color: #009e4d;
    }
    .footerInfo{
        text-align:center;
        border-top: 1px solid #efefef;
        clear: both;
        line-height: 60px;
    }
</style>


