<template>
  <div class="register">
    <loginHeader></loginHeader>
    <div class="content">
        <ul class="register-ul">
            <li>
                <i></i>
                <router-link to="/login"></router-link>
                <span>?</span>
            </li>

            <!--  -->
            <li class="hint" v-html="Prompt"></li>

            <!--   -->
            <!-- <li ref="phoneNum">
                <i class="iconfont icon-shouji"></i>
                <input 
                    type="text" 
                    ref="tel"
                    v-focus
                    placeholder="," 
                    maxlength="11"
                    v-model="phoneNum"
                    v-on:keyup.13='telEnter'
                    @focus="phoneFocus($event)"
                    @blur="phoneBlur($event)"
                />
                <span 
                    v-if="iconStatus" 
                    class="iconfont icon-iconfontshanchu"
                    @click="clearInput"
                >
                </span>
            </li> -->

            <!--   -->
            <li ref="phoneNum"><!-- input-active-warn -->
                <i class="iconfont icon-shouji"></i>
                <input 
                    type="text" 
                    ref="tel"
                    v-focus
                    placeholder="" 
                    maxlength="11"
                    v-model="phoneNum"
                    v-on:keyup.13='telEnter'
                    @focus="phoneFocus($event)"
                    @blur="phoneBlur($event)"
                />
                <span 
                    v-if="iconStatus" 
                    class="iconfont icon-iconfontshanchu"
                    @click="clearInput"
                >
                </span>
            </li>

            <!--  -->
            <li ref="codeNum"><!-- input-active-warn -->
                <i class="iconfont icon-icon-"></i>
                <input 
                    type="text" 
                    ref="verification"
                    placeholder="" 
                    maxlength="6"
                    v-model="codeNum"
                    v-on:keyup.13='verificationEnter' 
                    @focus="verificationFocus($event)"
                    @blur="verificationBlur($event)"
                />
                <a 
                    href="javascript:;" 
                    @click="getCode"
                    v-text="getCodeInfo"
                > 
                </a>
            </li>

            <!--  -->
            <li ref="passwordNum"><!-- input-active-warn -->
                <i class="iconfont icon-p-password"></i>
                <input 
                    :type="inputType" 
                    ref="password"
                    placeholder="" 
                    maxlength="16"
                    @input="passwordImport($event)"
                    v-on:keyup.13='passwordEnter' 
                    @focus="passwordFocus($event)"
                    @blur="passwordBlur($event)"
                />
                <span 
                    class="iconfont" 
                    :class="passIconType"
                    @click.stop="changePasswordType($event)" 
                >
                    
                </span>
            </li>

            <li>6-16</li>
            
            <!--  -->
            <li>
                <input 
                    type="radio" 
                    id="clause" 
                    :checked="checked"
                    @click="radio"
                 />
                <label for="clause">
                    <span></span>
                    <i class="pointer" @click="dialogVisible = true"></i>
                </label>
                
            </li>

            <!--  -->
            <li class="button-wrap" :class="{buttonActive:checked && phoneStatus && passwordStatus && codeStatus}">
                <input type="button" value="" @click="registerBtn" />
            </li>

        </ul>


        <el-dialog title="" v-model="dialogVisible" size="small" :close-on-click-modal='false' :close-on-press-escape='false'>
              <p style="line-height: 20px;height: 500px;overflow-y: scroll;">
                <span style="font-weight: bold;"></span><br/>
                <span style="font-weight: bold;"></span><br/>
                <br/>
                <br/>
                <br/>
                <br/>
                1.1<br/>
                1.2"<br/>
                <br/>
                <br/>
                2.1<br/>
                2.2,<br/>
                2.348<br/>
                2.4 <br/>
                <br/>
                <br/>
                3.1<br/>
                3.2<br/>
                3.32<br/>
                <br/>
                <br/>
                4.1  10000<br/>
                4.2  <br/>
                4.3  <br/>
                4.4  <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                15<br/>
                <br/>
                <br/>
                <br/>
                <br/>
                <br/>
                8.1<br/>
                15<br/>
                2<br/>
                8.2<br/>
                8.3<br/>
              </p>
            <input type="button" name="" @click="dialogVisible = false" value="" style="margin: 20px auto 0 auto;border:1px solid #D8D8D8;background: #009E4D;color: #fff;padding: 15px 60px;display: block;" />
        </el-dialog>
    </div>

    <index-footer></index-footer>

</div>
</template>

<script>
    import loginHeader from "@/components/common/login_header"
    import IndexFooter from "@/components/common/index_footer"
    export default {
        name: 'login',
        data () {
        return {
            dialogVisible:false,
            Prompt:'',                  // 

            inputType:'password',       // 
            passIconType:'icon-biyan',  //  icon
            iconStatus:false,           // X  

            phoneNum:'',                // 
            phoneStatus:false,          //  

            getCodeInfo:'',
            getCodeStatus:false,        //  
            wait:60,                    // 

            codeNum:'',                 // 
            codeStatus:false,           //  

            passwordNum:'',             // 
            passwordStatus:false,       //  

            checked:false,              //  

        }
    },
    components:{
        loginHeader,IndexFooter
    },
    directives:{
        focus: {
            inserted: function (el) {
                el.focus();
            }
        }
    },
    mounted(){
    },
    watch:{
        // 
        phoneNum:{
            handler(curval,oldval){
                // X 
                curval.length >=1 
                ?this.iconStatus = true
                :this.iconStatus = false;

                let tem = this.phoneNumberEntry(curval);

                switch(tem){
                    case '-1':
                        this.promptInfo('1');
                        this.$refs.phoneNum.setAttribute('class','input-active-warn');
                        this.phoneStatus = false;
                        return false;
                        break;

                    case '1':
                        this.promptInfo();
                        this.phoneStatus = true;
                        break;

                    case '3':
                        this.promptInfo('');
                        this.$refs.phoneNum.setAttribute('class','input-active-warn');
                        this.phoneStatus = false;
                        return false;
                        break; 

                    case '0':
                        this.promptInfo('');
                        this.$refs.phoneNum.setAttribute('class','input-active');
                        this.phoneStatus = false;
                        return false;
                        break;

                    case '2':
                        this.promptInfo();
                        this.$refs.phoneNum.setAttribute('class','input-active');
                        this.phoneStatus = false;
                        return false;
                        break;

                };
            },
            deep:true
        },
        
        //   
        phoneStatus:{
            handler(curval,oldval){
                curval ? this.getCodeStatus = true : this.getCodeStatus = false;

                // console.log(this.getCodeStatus)
            },
            deep:true
        },

        // 
        codeNum:{
            handler(curval,oldval){
                let tem = this.codeNumberEntry();
                if(tem == 0){
                    this.promptInfo('');
                    this.codeStatus = false;
                }else if(tem == 1){
                    this.promptInfo('');
                    this.codeStatus = true;
                }else{
                    this.codeStatus = false;
                }
            },
            deep:true
        },

        //  
        checked:{
            handler(curval,oldval){
                
                if(curval){
                    this.promptInfo('');
                }
            },
            deep:true
        },

        // 
        passwordNum:{
            handler(curval,oldval){
                let tem = this.passwordrEntry();
                if(tem == 0){
                    this.promptInfo('');
                    this.$refs.passwordNum.setAttribute('class','input-active');
                    this.passwordStatus = false;
                }else if(tem == 1){
                    this.promptInfo();
                    this.passwordStatus = true;
                }else if(tem == -2){
                    this.promptInfo('');
                    this.$refs.passwordNum.setAttribute('class','input-active-warn');
                    this.passwordStatus = false;
                }else{
                    this.promptInfo();
                    this.passwordStatus = false;
                }
            },
            deep:true
        }
    },
    methods:{
        //   Enter 
        telEnter(){
            this.$refs.verification.focus();
        },

        //   Enter 
        verificationEnter(){
            this.$refs.password.focus();
        },

        //   Enter 
        passwordEnter(){

        },

        //  
        changePasswordType(e){
            // if(this.inputType === 'password'){
            //     //  input 
            //     this.inputType = 'tel';
            //     //  icon 
            //     this.passIconType = 'icon-helpeye';
            // }else{
            //     this.inputType = 'password';
            //     this.passIconType = 'icon-biyan';
            // }

            if ($(e.currentTarget).hasClass('icon-biyan')) {
                $(e.currentTarget).removeClass('icon-biyan').addClass('icon-helpeye');
                $(e.currentTarget).prev('input').attr('type','tel')
            }else{
                $(e.currentTarget).removeClass('icon-helpeye').addClass('icon-biyan');
                $(e.currentTarget).prev('input').attr('type','password')
            }
        },

        // 
        radio(){
           this.checked = !this.checked;
        },

        //    
        phoneFocus(e){
            this.focus_Or_blur('phoneNum','focus');
        },
        phoneBlur(e){
            this.focus_Or_blur('phoneNum','');
            let tem = this.phoneNumberEntry(this.phoneNum);

            switch(tem){
                case '-1':
                    this.promptInfo('1');
                    this.phoneStatus = false;
                    this.$refs.phoneNum.setAttribute('class','input-active-warn');
                    return false;
                    break;

                case '0':
                    this.promptInfo('');
                    this.$refs.phoneNum.setAttribute('class','input-active-warn');
                    this.phoneStatus = false;
                    return false;
                    break;

                case '1':
                    this.promptInfo('');
                    this.phoneStatus = true;
                    break;

                case '2':
                    this.promptInfo('');
                    this.phoneStatus = false;
                    this.$refs.phoneNum.setAttribute('class','input-active-warn');
                    return false;
                    break;

                case '3':
                    this.promptInfo('');
                    this.phoneStatus = false;
                    return false;
                    break;             
            };
        },

        //    
        verificationFocus(e){
            this.focus_Or_blur('codeNum','focus');
        },
        verificationBlur(e){
            this.focus_Or_blur('codeNum','');
            if(this.codeNum.length == 0){

                this.promptInfo('');
                this.$refs.codeNum.setAttribute('class','input-active-warn');
                return false;

            }else if(this.codeNum.length < 4){
                this.promptInfo('');
                this.$refs.codeNum.setAttribute('class','input-active-warn');
                return false;
            }else{
                this.promptInfo('');
            }
        },

        //    
        passwordFocus(e){
            this.focus_Or_blur('passwordNum','focus');
        },
        passwordBlur(e){
            this.focus_Or_blur('passwordNum','');
            let tem = this.passwordrEntry();
            if(tem == 0){
                this.promptInfo('');
                this.$refs.passwordNum.setAttribute('class','input-active-warn');
            }else if(tem == 1){
                this.promptInfo();
            }else{
                this.promptInfo('');
                this.$refs.passwordNum.setAttribute('class','input-active-warn');
            }
        },

        // 
        getCode(){

            const _this = this;

            $.ajax({
                url:this.$api.register.send_validate_code,
                data:{
                    mobile:this.phoneNum,
                    scene:1,
                    type:'mobile'
                },
                dataType: "json",
                type: 'post',
                beforeSend:function () {
                    if(_this.getCodeInfo != ""){

                        return false;
                    }
                    if(!_this.getCodeStatus){
                        _this.promptInfo('');
                        return false;
                    }
                },
                success: function (res) {

                    if (res.status == '1') {
                        _this.promptInfo(res.msg,2);
                        _this.time();
                    }else{
                        _this.promptInfo(res.msg);
                    }
                },
                error:function (res) {
                    _this.$anchu_message.error(res.msg);
                }
            })
        },

        // 
        registerBtn(){

            if(!this.phoneStatus){
                this.promptInfo('');
                return false;
            }

            if(!this.codeStatus){
                this.promptInfo('');
                return false;
            }

            if(!this.passwordStatus){
                this.promptInfo('');
                return false;
            }

            if(!this.checked){
                this.promptInfo('');
                return false;
            }

            let _this = this;

            $.ajax({
                url: this.$api.register.reg,
                data: {
                    user_name: this.phoneNum,
                    code: this.codeNum,
                    password: this.passwordNum
                },
                type: 'post',
                dataType:"json",
                success: function (res) {
                    _this.promptInfo(res.msg);
                    if (res.code == 1){
                        // id
                        _this.$cookie.setCookie('a34ea2d96d4239d4d86803bb3cd99788',res.result.user_id,'d1');
                        // 
                        _this.$cookie.setCookie('8e3c824e1d6254b74a013799c1565538',_this.phoneNum,'d1');
                        // 
                        _this.$cookie.setCookie('b61a9d162ac1dfaa22d0f05bdc7f6299','-4','d1');
                        //
                        dplus.track("","","");
                        //  proof
                        _this.$router.push({path:'/proof'});
                    }
                },
                error:function (res) {
                    _this.$anchu_message.error(res.msg);
                }
            });

        },

        // 
        time() {

            const _this = this;

            if (_this.wait <= 0) {

                _this.getCodeInfo="";
                _this.wait = 60;
                _this.getCodeStatus = true;

            } else {

                _this.getCodeStatus = false;
                _this.getCodeInfo = "" + _this.wait + "s";
                _this.wait--;

                let timeOut = setTimeout(function () {

                    _this.time();

                    if(_this.wait <= 0){
                        clearTimeout(timeOut); 
                    }
                },
                1000)
            }
        },

        // 
        passwordImport(e){
            this.passwordNum = this.$refs.password.value;

            let tem = this.passwordrEntry();
            if(tem == 0){
                this.promptInfo('');
                this.$refs.passwordNum.setAttribute('class','input-active-warn');
            }else if(tem == 1){
                this.promptInfo();
            }else{
                this.promptInfo();
            }
            
        },

        // 
        phoneImport(){
            let tem = this.phoneNumberEntry(this.phoneNum);

            switch(tem){
                case '-1':
                    this.promptInfo('1');
                    this.phoneStatus = false;
                    this.$refs.tel.setAttribute('class','input-active-warn');
                    return false;
                    break;

                case '1':
                    this.promptInfo('');
                    this.phoneStatus = true;
                    break;

                case '3':
                    this.promptInfo('');
                    this.$refs.phoneNum.setAttribute('class','input-active-warn');
                    this.phoneStatus = false;
                    return false;
                    break;             
            };
        },

        // input
        clearInput(){
            this.phoneNum = '';
            this.iconStatus = false;
            this.promptInfo();
            this.$refs.tel.focus();
        },

        
        /**
        *
        *    
        *    type 1  2 2017/9/15
        *
        */
        promptInfo(info = '',type = 1){
            if(info == ''){
                this.Prompt =  '';
            }else{
                let Info = `
                    <div class='hint-div hint-div${type}'>
                        <i class='iconfont icon-warning'></i>
                        <span>${info}</span>
                    </div>
                `;
                this.Prompt = Info;
            }

        },

        

        //     
        focus_Or_blur(name,value = 'focus'){

            if(name === 'phoneNum'){

                value === "focus"
                ? this.$refs.phoneNum.setAttribute('class','input-active')
                :this.$refs.phoneNum.removeAttribute('class');
                return false;
            }

            if(name === 'codeNum'){
                value === "focus"
                ? this.$refs.codeNum.setAttribute('class','input-active')
                :this.$refs.codeNum.removeAttribute('class');
                return false;
            }

            if(name === 'passwordNum'){
                value === "focus"
                ? this.$refs.passwordNum.setAttribute('class','input-active')
                :this.$refs.passwordNum.removeAttribute('class');
                return false;
            }
        },

        // 
        phoneNumberEntry(num){

            let reg = /^1[3|4|5|7|8][0-9]\d{4,8}$/i;

            
            if(num.length > 0 && num.substring(0,1) != 1){
                return '-1';                                    // 1
            }
            if(num.length == 11 && reg.test(num)){
                return '1';                                     // 
            }
            if(num.length == 11 && !reg.test(num)){             // 
                return '3';
            }
            if(num.length > 11){                                // 
                return '3';
            }
            if(num.length == 0){
                return '0';                                     // 
            }

            return '2';                                           // 
        },

        // 
        codeNumberEntry(){
            const reg = /^\d{4,6}$/i;
            if(this.codeNum.length == 0){
                return 0;
            }else if(reg.test(this.codeNum)){
                return 1;
            }else{
                return -1;
            }
        },

        // 
        passwordrEntry(){
            const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)(?!([^(0-9a-zA-Z)]|[\(\)])+$)([^(0-9a-zA-Z)]|[\(\)]|[a-zA-Z]|[0-9]).{4,17}$/i;
            if(this.passwordNum.length == 0){
                return 0;
            }else if(this.passwordNum.length == 16 && !reg.test(this.passwordNum)){
                return -2;
            }else if(reg.test(this.passwordNum)){
                return 1;
            }else{
                return -1;
            }
        },

    }
}

</script>

<style>/*scoped .hint .hint-div login*/
    .hint-div2{
        border-color: #009e4d;
        color:#009e4d;
        background: #c8f0db;
    }
    input::-ms-clear, input::-ms-reveal{
        display: none;
    }
    .register{width:100%;height:100%}
    .register .content{
        height:600px;
        background:#F0F0F0 url(/static/images/login_bg.png) no-repeat center 0;
    }
    .register-ul{
        width:350px;
        float:right;
        margin:100px 15% 0 0;
        padding:20px 40px;
        background:#fff;
        line-height: 26px;
    }
    .register-ul li:nth-child(1){
        overflow: hidden;
    }
    .register-ul li:nth-child(1) i{ float: left; font-size: 20px; }
    .register-ul li:nth-child(1) span{ float: right; }
    .register-ul li:nth-child(1) a{ float: right; color: #009e4d; }

    .register-ul li:nth-child(3),
    .register-ul li:nth-child(4),
    .register-ul li:nth-child(5){
        height: 46px;
        line-height: 46px;
        overflow: hidden;
        border:1px solid #d8d8d8;
        margin-bottom:20px;
    }
    .register-ul li:nth-child(5){
        margin-bottom:0;
    }
    .register-ul li:nth-child(3) i,
    .register-ul li:nth-child(4) i,
    .register-ul li:nth-child(5) i{
        float: left;
        text-align: center;
        width: 50px;
        border-right: 1px solid #d8d8d8;
        background-color: #f2f2f2;
    }
    .register-ul li:nth-child(3) input,
    .register-ul li:nth-child(4) input,
    .register-ul li:nth-child(5) input{
        float: left;
        height: 100%;
        width: 180px;
        padding-left: 16px;
        font-size: 14px;
    }
    .register-ul li:nth-child(3) span,
    .register-ul li:nth-child(5) span{
        float: right;
        height: 100%;
        margin: 0 17px;
        color: #9a9a9a;
        cursor: pointer;
    }
    .register-ul li:nth-child(5) span{
        color: #333;
    }

    .register-ul li:nth-child(4) a{
        font-size: 14px;
        padding-left: 16px;
        position: relative;
    }
    .register-ul li:nth-child(4) a:after{
        position: absolute;
        content: '';
        width: 1px;
        height: 150%;
        left: 0;
        top: -25%;
        background: #d8d8d8;
    }
    .register-ul li:nth-child(6){
        font-size: 12px;
        color: rgb(153, 153, 153);
    }
    .register-ul li:nth-child(7) input{
        color: rgb(153, 153, 153);
        font-size: 12px;
        vertical-align: middle;
    }
    .register-ul li:nth-child(7) label{
        color: rgb(153, 153, 153);
        font-size: 12px;
    }
    .register-ul li:nth-child(7) i{
        font-size: 12px;
        color: #333;
    }
    .register-ul li:nth-child(7) i:hover{
        color: #009e4d;
    }

    /**/
    .input-active-warn{
        border:1px solid red !important;
    } 
    .input-active-warn i{
        border-right: 1px solid red !important;
        color: red;
    }

    .input-active-warn input::-webkit-input-placeholder{/* WebKit browsers */ 
        color: red;
    }
    .input-active-warn input:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */ 
        color: red;
    }
    .input-active-warn input::-moz-placeholder { /* Mozilla Firefox 19+ */ 
        color: red;
    } 
    .input-active-warn input:-ms-input-placeholder { /* Internet Explorer 10+ */ 
        color: red;
    }

    .input-active{
        border:1px solid #009e4d !important;
    }
    .input-active i{
        border-right: 1px solid #009e4d !important;
        color: #009e4d;
    }

    .button-wrap{
        margin-top:10px;
        height: 46px;
        border-radius: 4px;
        background: #f2f2f2;
        line-height: 46px;
    }
    .button-wrap input{
        width: 100%;
        height: 100%;
        color: #999;
        font-size: 20px;
    }
    .buttonActive{
        background: #009448;
    }
    .buttonActive input{
        color: #fff;
    }
</style>


