<template>
	<div class="address">

		<div class="address-header">{{addOredit ? '' : ''}}</div>

		<div class="address-wrap">
			<!--  -->
			<p v-if="addOredit">
				<label></label>
				
				<!-- 
					idchange
					 change
					 visible-change
				-->
                <el-select 
                    v-model="addform.selProvince" 
                    placeholder=""
                    @change="electCity"
                >
                    <el-option
                        v-for="(item,index1) in addform.province"
                        :label="ellipsis(item.name,10)"
                        :value="item.id"
                        key="index1" 
                    >
                    </el-option>
                </el-select>

                <el-select 
                    v-model="addform.selCity" 
                    placeholder=""
                    @change="electArea"
                >
                    <el-option
                        v-for="(item,index2) in addform.city"
                        :label="ellipsis(item.name,10)"
                        :value="item.id"
                        key="index2" 
                    >
                    </el-option>
                </el-select>

                <el-select v-model="addform.selArea" placeholder="/">
                    <el-option
                        v-for="(item,index3) in addform.district"
                        :label="ellipsis(item.name,10)"
                        :value="item.id"
                        key="index3"
                    >
                    </el-option>
                </el-select>
			</p>
			
			<!--  -->
			<p v-if="!addOredit">
				<label></label>
				
				<!-- 
					idchange
					 change
					 visible-change
				-->
                <el-select 
                    v-model="editform.selProvince" 
                    placeholder=""
                    @visible-change="editCity"
                >
                    <el-option
                        v-for="(item,index1) in addform.province"
                        :label="ellipsis(item.name,10)"
                        :value="item.id"
                        key="index1" 
                    >
                    </el-option>
                </el-select>

                <el-select 
                    v-model="editform.selCity" 
                    placeholder=""
                    @visible-change="editArea"
                >
                    <el-option
                        v-for="(item,index2) in addform.city"
                        :label="ellipsis(item.name,10)"
                        :value="item.id"
                        key="index2" 
                    >
                    </el-option>
                </el-select>

                <el-select v-model="editform.selArea" placeholder="/">
                    <el-option
                        v-for="(item,index3) in addform.district"
                        :label="ellipsis(item.name,10)"
                        :value="item.id"
                        key="index3"
                    >
                    </el-option>
                </el-select>
			</p>

			<p class="textarea">
                <label></label>
                <textarea 
	                maxlength="30" 
	                v-model="addform.address"
	                placeholder=""
                ></textarea>
            </p>

            <p class="name">
                <label></label>
                <input 
                	type="text" 
                	maxlength="10" 
                	v-model="addform.consigneename" 
                	placeholder="XX" 
                />
            </p>

            <p class="name">
                <label></label>
                <input 
	                type="text" 
	                maxlength="11" 
	                v-model="addform.phone"
	                placeholder="" 
                />
            </p>

            <p class="button">
                <button @click="saveAdd"></button>
                <button @click="emptyAdd"></button>
            </p>


		</div>

		<dl class="address-list" v-for="(item,index) in addressList" :key="index">
			<dt>
				<i v-text="ellipsis(item.consignee,6)"></i>
				<i v-text="item.mobile"></i>
				<i v-text="ellipsis(item.province_name,3)"></i>
				<i v-text="ellipsis(item.city_name,4)"></i>
				<i v-text="ellipsis(item.district_name,4)"></i>
				<i v-text="ellipsis(item.address,18)"></i>
			</dt>
			<dd v-if="item.is_default == 1"><em></em></dd>
			<dd class="operation">
				<span v-if="item.is_default == 0" @click="toDefault(item.address_id,index)"></span>
				<i @click="editDress(index)"></i>
				<i @click="deleteAddress(item.address_id,index)"></i>
			</dd>
		</dl>

	</div>
</template>

<script>
	export default {
		name: 'address',
		data () {
			return {
				addressList:[],			// 
				addOredit:true, 		// true false 

				addform:{
					province:[],
					city:[],
					district:[],

					selProvince:'',				//  id
					selCity:'',					//  id
					selArea:'',					//  id

					address:'',                 //   
	                consigneename:'',           //   
	                phone:''                    //  
				},

				editform:{
					id:'',	        			// id

					//   addform 
					// province:[],
					// city:[],
					// district:[],

					selProvince:'',				//  id
					selCity:'',					//  id
					selArea:'',					//  id

					//   addform 
					// address:'',                 //   
				    // consigneename:'',           //   
				    // phone:''                    //  
				}
			}
		},
		beforeCreate(){
  		
	        let proof  = this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');

	        if(proof == 1 ){

	        }else if(proof == -1){            // 

	        	this.$anchu_message.warn('');
	        	this.$router.push('/');

	        }else if(proof == -2 || proof == -3 ){     //  

	        	this.$anchu_message.error('');
	            this.$router.push('/proof');

	        }else if(proof == -4 ){     // 

	        	this.$anchu_message.warn('');
	            this.$router.push('/proof');

        	}else{

	        	this.$router.push('/login');
	        }
		},
		mounted(){
			this.getAddressList();
			this.selectAddressInfo();
		},
		methods:{
			// 
			saveAdd(){
				if(!this.addOredit){
					this.addressEdit();
					return false;
				}
				let _this = this;
				$.ajax({
					url: this.$api.address.create,
					data:{
						province: 	this.addform.selProvince,
					 	city: 		this.addform.selCity,
					 	district: 	this.addform.selArea, 
					 	address: 	this.addform.address, 
					 	consignee: 	this.addform.consigneename, 
					 	mobile: 	this.addform.phone
					},
					dataType: "json",
					type: 'post',
					beforeSend:function(){

						if (_this.addform.selProvince =='' || _this.addform.selCity == '' || _this.addform.selArea == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						if (_this.addform.address == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						if (_this.addform.consigneename == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						if (_this.addform.phone == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						// 
	                    const reg = /^1(3|4|5|7|8)\d{9}$/; //(711)
	                    if(!reg.test(_this.addform.phone)){
	                        _this.$anchu_message.error('');
	                        return false;
	                    }
					},
					success: function (res) {
						if (res.error_code == 'FAILED') {
							_this.$anchu_message.error(res.error_msg);
						}else{
							_this.$anchu_message.success('');
							_this.emptyAdd();
							_this.getAddressList();
						}
					},
					error:function (res) {
						_this.$anchu_message.error('');
					}
				});
			},

			// 
	        addressEdit(){
	            let _this = this;
	            let Data = {
	                id: 		this.editform.id,

	                province: 	this.editform.selProvince, 
	                city: 		this.editform.selCity,
	                district: 	this.editform.selArea, 

	                address: 	this.addform.address, 
	                consignee: 	this.addform.consigneename,
	                mobile: 	this.addform.phone
	            };


	            $.ajax({
	                type: "post",
	                url: this.$api.address.update,
	                data:Data,
	                dataType: "json",
	                beforeSend: function (res) {

	                	if(_this.editform.id = ''){
	                		_this.$anchu_message.warn('');
							return false;
	                	}

	                    if (_this.editform.selProvince =='' || _this.editform.selCity == '' || _this.editform.selArea == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						if (_this.addform.address == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						if (_this.addform.consigneename == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						if (_this.addform.phone == '') {
							_this.$anchu_message.warn('');
							return false;
						}

						// 
	                    const reg = /^1(3|4|5|7|8)\d{9}$/; //(711)
	                    if(!reg.test(_this.addform.phone)){
	                        _this.$anchu_message.error('');
	                        return false;
	                    }
	                    
	                },
	                success:function(res){
	                    let errMess = res.error_msg;
	                    if(errMess){
	                        _this.$anchu_message.warn(errMess);
	                    }else{
	                        _this.$anchu_message.success('');
	                    }
	                    _this.getAddressList();

	                    //  
	                    _this.addOredit = true;
	                    _this.emptyAdd();
	                    
	                }
	            })
	        },

			//    
	        selectAddressInfo(id = 0){
	            let _this = this;
	            $.ajax({
	                url: this.$api.batchList.getregion,
	                data:{
	                    parent_id:id
	                },
	                dataType:"json",
	                type: 'get',
	                success: function (res) {
	                    let sitData = res.data;
	                    if(!sitData){
	                    	// 
	                    	_this.$anchu_message.error('');
	                    	return false
	                    }

	                    switch(sitData[0].level){
	                        case 1:
	                            _this.addform.province = sitData;
	                            break;
	                        case 2:
	                            _this.addform.city = sitData;
	                            break;
	                        case 3:
	                            _this.addform.district = sitData;
	                            break;
	                    }
	                }
	            })
	        },

	        // 
	        editDress(index){
	        	this.addOredit = false;
	        	const data = this.addressList[index];

	        	//    
	        	this.selectAddressInfo(data.city);
	        	this.selectAddressInfo(data.province);

	        	// id    id
	        	this.editform.id = data.address_id; 
	        	this.editform.selProvince = Number(data.province);
	        	this.editform.selCity = Number(data.city);
	        	this.editform.selArea = Number(data.district);

	        	//   
	        	this.addform.address = data.address;
	        	this.addform.consigneename = data.consignee;
	        	this.addform.phone = data.mobile;
	        },

	        // 
	        deleteAddress(id,index){
				let _this = this;
				$.ajax({
					url: this.$api.address.delete,
					data:{id:id},
					dataType: "json",
					type: 'post',
					success: function (res) {
						if (res.error == 0) {
							_this.$anchu_message.success('');
							_this.addressList.splice(index,1);
						}else{
							_this.$anchu_message.success('');
						}
					},
					error:function (res) {
						_this.$anchu_message.error('');
					}
				});
			},

	        // 
	        toDefault(id,index){
				let _this = this;
				$.ajax({
					url: this.$api.address.set_default,
					data:{id:id},
					dataType: "json",
					type: 'post',
					success: function (res) {
						if (res.error == 0) {
							_this.$anchu_message.success('');
							for (let i = 0; i < _this.addressList.length; i++) {
								_this.addressList[i].is_default = 0;
							}
							_this.addressList[index].is_default = 1;
						}
					},
					error:function (res) {
						_this.$anchu_message.error('');
					}
				});
			},

	        // 
	        getAddressList(){
				let _this = this;
				$.ajax({
					url: this.$api.goodsDetails.getlist,
					dataType: "json",
					type: 'get',
					success: function (res) {
						_this.addressList = res.data;
					},
					error:function (res) {
						_this.$anchu_message.error('');
					}
				});
			},

			// 
			emptyAdd(){
				this.addform.selProvince = '';
				this.addform.selCity = '';
				this.addform.selArea = ''; 
				this.addform.address = ''; 
				this.addform.consigneename = ''; 
				this.addform.phone = '';
				this.addform.city = [];
				this.addform.district = [];
				this.addOredit = true;
			},

			//  id 
	        electCity(value){
	        	if(value == ''){
	        		return false
	        	}
	        	this.selectAddressInfo(value);

	        	//     id 
	        	this.addform.selCity = '';
	        	this.addform.city = [];
					
	        	this.addform.selArea = '';
	        	this.addform.district = [];
	        },
	        //  id 
	        electArea(value){
	        	if(value == ''){
	        		return false
	        	}
	        	this.selectAddressInfo(value);
	        	//    id 
	        	this.addform.selArea = '';
	        	this.addform.district = [];
	        },

	        //  id  value=false 
	        editCity(value){
	        	if(!value){
	        		this.selectAddressInfo(this.editform.selProvince);

	        		//     id     
	        		this.editform.selCity = '';
	        		this.addform.city = [];

	        		this.editform.selArea = '';
	        		this.addform.district = [];
	        	}
	        },
	        //  id  value=false 
	        editArea(value){
	        	if(!value){
	        		this.selectAddressInfo(this.editform.selCity);

	        		//    id     
	        		this.editform.selArea = '';
	        		this.addform.district = [];
	        	}
	        },

	        // 
	        ellipsis(world,n){

	            if(world && world.length > n){
	                return world.slice(0,n) + "..."
	            }else{
	                return world;
	            }
	        }
		}
	}
</script>

<style lang="scss" scoped>
	$class-pre:'address';
	.#{$class-pre}{
		min-height: 380px;
	    background: #fff;
	    float: left;
	    position: relative;
	    width: 1017px;
	    padding: 0 0 98px 41px;
		&-header{
			text-align: left;
		    border-bottom: 1px solid #d8d8d8;
		    padding-bottom: 12px;
		    font-weight: 900;
		}
		&-wrap{
			padding-top: 10px;
			vertical-align: top;
			p{
				padding: 10px 0;
				padding-left: 128px;
				-o-box-sizing: border-box;
			    -ms-box-sizing: border-box;
			    box-sizing: border-box;
			    position: relative;
				label{
					width: 128px;
					display: inline-block;
					text-align: right;
					position: absolute;
					top: 16px;
					left: 0;
					&:before{
						content:'*';
						color: red;
					}
				}
				button{
					border: 1px solid #d8d8d8;
				    background: #f2f2f2;
				    padding: 5px 15px;
				    margin-right: 10px;
				    cursor: pointer;
				    &:focus {
					    outline: 0;
					}
				}
			}
			.name{
				input{
					border: 1px solid #d8d8d8;
				    width: 656px;
				    height: 35px;
				    padding: 0 10px;
				    -o-box-sizing: border-box;
				    -ms-box-sizing: border-box;
				    box-sizing: border-box;
				}
			}
		}
		&-list{
			overflow: hidden;
			line-height: 40px;
		    margin-top: 8px;
		    -o-box-sizing: border-box;
		    -ms-box-sizing: border-box;
		    box-sizing: border-box;
		    border: 2px solid #f3f3f3;
		    cursor: pointer;
			dt{
				float: left;
				margin-right: 20px;
				i{
					margin-left: 10px;
				}
			}
			dd{
				float: left;
				margin-left: 10px;
				i,span{
					margin-right: 10px;
					color: #009e4d;
				}
				span{
				    display: inline-block;
				    height: 25px;
				    line-height: 25px;
				    padding: 0 5px;
				    background: #ebebeb;
				}
				em{
					color: #999;
				    display: inline-block;
				    height: 25px;
				    line-height: 25px;
				    padding: 0 5px;
				    background: #ebebeb;
				    cursor: text;
				}
			}
			.operation{
				display: none;
			}
			&:hover .operation{
				display: block;
			}
		}
	}

	.textarea{
		textarea{
			border: 1px solid #d8d8d8;
		    resize: none;
		    width: 656px;
		    height: 96px;
		    padding: 10px;
		    -o-box-sizing: border-box;
		    -ms-box-sizing: border-box;
		    box-sizing: border-box;
		}
	}
</style>

<style>
    /*  */
    .address-wrap .el-input__inner{
        height: 35px;
        width: 200px;
        border: 1px solid #d8d8d8;
        font-size: 12px;
        -moz-border-radius: 0;
        -webkit-border-radius: 0;
        border-radius: 0;
        padding-right: 30px!important;
    }
</style>


	


