<template>
	<div class="t-header-top">
		<div class="header-top">
			<div></div>
			<div>
				<router-link v-if="loginstatus"  to="/mine/myIndent">{{username}}</router-link>
				<span v-if="loginstatus" class="pointer" @click="tosignout"></span>
				<router-link v-if="!loginstatus" to="/login" @click.native="preLogin()">[]</router-link>
				<router-link v-if="!loginstatus" to="/register">[]</router-link>
				<span class="iconfont icon-shuxian"></span>
				<router-link to="/mine/myIndent" @click.native="nextMine()"></router-link>
				<!-- <span @click="goMine" class="pointer"></span>  -->
				<span class="iconfont icon-shuxian"></span>
				<router-link tag="span" to="/mine/myIndent" class="pointer" @click.native="nextMine()"></router-link>
				
				<!-- <span @click="goMine" class="pointer"></span>  -->
				<span class="iconfont icon-shuxian"></span>

				<!-- <span class="pointer"></span> -->
				<router-link class="pointer" tag="span" to="/help/origin"></router-link>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 't-header-top',
		data(){
			return {
				username:this.$cookie.getCookie('8e3c824e1d6254b74a013799c1565538'),
				loginstatus:false,
				nowPath:''
			}
		},
		mounted(){
			//
			let nowHash = this.$route.fullPath;
			this.nowPath = nowHash;

			if(this.username){
				this.loginstatus = true;
			}else{
				this.loginstatus = false;
			}
		},
		methods:{
			preLogin() {
			    this.$store.state.previousPath = this.nowPath;   
			},
			nextMine() {
				this.$store.state.nextMinePath = '/mine/myIndent'
			},
			tosignout() {
				let _this = this;
				$.ajax({
					url: _this.$api.user.logout,
					dataType: "json",
					type: 'post',
					success: function (res) {
						if (res.code == 1){
							// userId
                            _this.$cookie.delCookie('a34ea2d96d4239d4d86803bb3cd99788');
                            // 
                            _this.$cookie.delCookie('8e3c824e1d6254b74a013799c1565538');
                            // 
                            _this.$cookie.delCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');
                            // PHPSESSID
                            _this.$cookie.delCookie('PHPSESSID');

                            _this.username= null;
							_this.loginstatus = false;

							_this.$store.state.nextNum = 1;

							_this.$router.go(0);
							
						}else{
							_this.$anchu_message.info(res.error_msg);
						}
					},
					error:function (res) {
						_this.$anchu_message.error('');
					}
				});
			}
			 
		}
	}
</script>

<style scoped>
	.t-header-top{
		width: 100%;
		background: #F8F8F8;
	}
	.t-header-top .header-top{
		font-size: 14px;
		width: 1200px;
		margin: 0 auto;
		height: 28px;
		line-height: 28px;
		display: flex;
		justify-content: space-between;
	}
</style>


