<template>
<div class="refunds">
    <p class="refunds-title" v-if="errorInfo < 0"></p>

    <dl v-if="errorInfo > 0">
      <dt class="refunds-title"></dt>
      <dd>
        <i></i>
        <p class="refunds-retreat">
          <!-- <span 
            v-bind:class="{refundsactive:index==classSign}" 
             
            v-for="(style,index) in refundsStyle" key="index" v-text="style">
            
          </span> --><!-- @click="refundsStylefun(index)" -->
          <span v-if="refundsInfo.value == 0 "></span>
          <span v-if="refundsInfo.value == 1 "></span>
        </p>
      </dd>

      <dd v-if="orderInfo.orderSt == 2">
        <i></i>
        <p>
          <button class="affirm-btn" @click="affimreceive"></button>
          <span class="affirm-btn-alert"></span>
        </p>
      </dd>

      <dd>
        <i></i>
        <p class="refunds-number">
          <input v-model="refundsInfo.number" type="number" placeholder="" />
        </p>
      </dd>

      <dd>
        <i></i>
        <p class="refunds-price">
          <input type="number"  v-model="refundsInfo.sum" placeholder="" />
          <span><b></b><i> {{orderInfo.orderAm}} </i></span>
        </p>
      </dd>

      <dd>
        <i></i>
        <p class="refunds-reason">
          <select v-model="refundsInfo.reason">
            <option value=""></option>
            <option value="/">/</option>
            <option value="/">/</option>
            <option value=""></option>
            <option value="/">/</option>
            <option value=""></option>
          </select>
        </p>
      </dd>

      <dd>
        <i></i>
        <p class="refunds-explain">
          <textarea placeholder="500" v-model="refundsInfo.explain"></textarea>
        </p>
      </dd>

      <dd v-if="refundsInfo.value != 0" >
        <i></i>

        <p class="refunds-uploadimg">

          <el-upload
            class="upload-demo"
            action="/api.php?s=api/uploadify/upload_image"
            :data="{'path':'user'}"
            :file-list="imgarr"
            :on-success="successLoading"
            :on-remove="removeLoading"
            accept="image/jpeg,image/jpg,image/png"
            >
            <el-button size="small" v-if="imgarr.length<3"></el-button>
            <div v-if="imgarr.length<3" slot="tip" class="el-upload__tip">           
              <p>3jpg/png3M</p>
            </div>
          </el-upload>
        </p>
      </dd>

      <dd>
        <p class="refunds-submit-btn">
          <button @click="submitAppy"></button>
        </p>
      </dd>
    </dl>

    <div class="refunds-alert" v-if="errorInfo < 0">
        <p><img src="/static/images/kongb.png"></p>
        <p></p>
    </div>
</div>
</template>

<script>

export default {
  name: 'refunds',
  data () {
    return {
      classSign:'0',
      refundsInfo:{
        number:'',
        sum:'',
        reason:'',
        explain:'',
        value:0
      },
      imgarr:[],
      errorInfo:2,
      orderInfo:{
        orderId:'', // id
        orderSn:'', // 
        orderAm:0,  // 
        orderSt:'' // 
      },
      goodsInfo:[]
      
    }
  },
    beforeCreate(){

        let proof  = this.$cookie.getCookie('b61a9d162ac1dfaa22d0f05bdc7f6299');

        if(proof == 1 ){

        }else if(proof == -1){            // 

            this.$anchu_message.warn('');
            this.$router.push('/');

        }else if(proof == -2 || proof == -3 ){     //  

            this.$anchu_message.error('');
            this.$router.push('/proof');

        }else if(proof == -4 ){                   // 

            this.$anchu_message.warn('');
            this.$router.push('/proof');

        }else{
            this.$router.push('/login');
        }


    },
  watch:{
    'refundsInfo.sum':{
      handler(curVal,oldVal){

        //   
        if(Number(curVal) > this.orderInfo.orderAm){
          this.refundsInfo.sum = this.orderInfo.orderAm;
        }

        if(Number(curVal) < 0 ){
          this.refundsInfo.sum = 0;
        }

      },
      deep:true
    }
  },
  created(){
    this.getOrderInfo();
  },
  mounted(){
    $('body,html').animate({scrollTop:0},10);
  },
  methods:{
    // 
    getOrderInfo(){
      const _this = this;
      $.ajax({
        type:"get",
        url:_this.$api.order.get_order_goods,
        data:{
          goods_id:_this.$route.query.goods_id,
          order_sn:_this.$route.query.order_sn,
          goods_spec:_this.$route.query.goods_spec
        },
        dataType:"json",
        success:function(res){
          // console.log(res)
          if(res.error == 0){// 
            _this.goodsInfo = res.data;
            _this.errorInfo = 2;
            _this.orderInfo.orderSt = res.data.order_status;
            _this.orderInfo.orderAm = res.data.total_amount;
            _this.orderInfo.orderId = res.data.order_id;

            // 1 2 10
            if(_this.orderInfo.orderSt == 1 ){

              _this.refundsInfo.value = 0;

            }else if(_this.orderInfo.orderSt == 2 ){

              _this.refundsInfo.value = 1;

            }else if(_this.orderInfo.orderSt == 10 ){

              _this.refundsInfo.value = 1;

            }
              
          }else{
            _this.errorInfo = -1;
          }
        }
      });
    },
    // 
    affimreceive(){
      let _this = this;
      $.ajax({
        type:"post",
        url:_this.$api.order.confirm,
        data:{
          id:_this.orderInfo.orderId
        },
        dataType:"json",
        success:function(res){
          if(res.error == 0){
            _this.$anchu_message.warn('');
            _this.orderInfo.orderSt = 10;
          }else{
            _this.$anchu_message.warn(data.error_code);
          }
        }
      });

    },
    inputfilters(){
      if(this.refundsInfo.sum > this.orderInfo.orderAm){
        this.refundsInfo.sum = this.orderInfo.orderAm;
      }
    },
    successLoading(file){
        if(file.code > 0){  // 

          this.$anchu_message.success(file.msg);
          this.imgarr.push(file.url);

        }else{  // 
          this.$anchu_message.error('');
        }
    },
    // 
    removeLoading(file){
      for(var i in this.imgarr){
        
        if(this.imgarr[i] == file.response.url){
          this.imgarr.splice(i,1);
        }
        
      }
    },
    // 
    submitAppy(){
      const _this = this;
      $.ajax({
        url: this.$api.order.return_goods,
        data: {
          order_id : _this.goodsInfo.order_id,
          order_sn : _this.$route.query.order_sn,
          goods_spec:_this.$route.query.goods_spec,
          goods_id:_this.goodsInfo.goods_id,
          type : _this.refundsInfo.value,
          reason : _this.refundsInfo.reason,
          imgs : _this.imgarr.join(),
          num : _this.refundsInfo.number,
          price : _this.refundsInfo.sum,
          comment:_this.refundsInfo.explain
        },
        type: 'post',
        dataType:"json",
        beforeSend: function () {
          if(_this.orderInfo.orderSt == 2){

            _this.$anchu_message.warn('');
            return false;

          }else if( _this.refundsInfo.reason == ''||
              _this.refundsInfo.number == ''||
              _this.refundsInfo.sum==''||
              _this.refundsInfo.explain=='' ){

                _this.$anchu_message.warn('');
                return false;
          }
        },
        success: function (res) {
          if(res.error_code){
            _this.$anchu_message.error('');
          }else{

            _this.$anchu_message.success(res.data);

            _this.$router.push({path:'/mine/refundDetail',
              query:{
                goods_id:_this.$route.query.goods_id,
                order_sn:_this.$route.query.order_sn,
                spec_key:_this.$route.query.goods_spec,
                return_sn:''
              }});
            
          }
        }
      });
    },
    // 
    deleteImg(index){
      this.imgarr.splice(index,1);
    },
    refundsStylefun(index){
      this.classSign = index;
      this.refundsInfo.value = index;
    }
  }
}
</script>

<style scoped>
.affirm-btn-alert{
  background: #009e4d;
  display: inline-block;
  padding: 5px 10px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  position: relative;
  margin-left: 20px;
  color: #fff;
}
.affirm-btn-alert:after{
  content: '';
  position: absolute;
  border: 7px solid transparent;
  border-right-color:#009e4d;
  left: -14px;
  bottom: 6px; 
}
.affirm-btn{
  border: 1px solid #009e4d;
  padding: 7px 9px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  font-size: 12px;
  cursor: pointer;
}
.upload-demo{
  width: 400px;
}
.refunds-alert{
  padding: 100px 0 100px;
  text-align: center;
}
/*.refunds-retreat .refundsactive{
  border: 1px solid #009e4d;
  background: url("/static/images/chose_yes.png") no-repeat right bottom;
}*/
.img_upload_file{
  width: 0;
  height: 0;
  visibility: hidden;
}
.refunds-submit-btn button{
  background: #009e4d;
  width: 160px;
  height: 46px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
}
.refunds-uploadimg-bottom i{
  color: #009e4d;
}
.refunds-uploadimg-bottom b{
  /*font-weight: 100;*/
  color: #009e4d;

}
.refunds-uploadimg-bottom{
  display: block;
  clear: both;
  padding-top: 8px;
}
.refunds-uploadimg label{
  font-size: 20px;
  background: linear-gradient(to bottom,#fefefe 0,#f3f3f3 100%);
  background: -webkit-linear-gradient(top,#fefefe 0,#f3f3f3 100%);
  background: -moz-linear-gradient(top,#fefefe 0,#f3f3f3 100%);
  background: -o-linear-gradient(top,#fefefe 0,#f3f3f3 100%);
  border: 1px solid #d8d8d8;
  width:70px;
  height: 70px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  float: left;
  cursor: pointer;
  padding: 8px 0 0 12px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.refunds-uploadimg span i:hover{
  color: #009e4d;
}
.refunds-uploadimg span i{
  position: absolute;
  top: 0;
  right: 0;
  color: #999;
  cursor: pointer;
}
.refunds-uploadimg span img{
  width:100%;
  height: auto;
}
.refunds-uploadimg span{
  position: relative;
  display: inline-block;
  width:70px;
  height: 70px;
  border: 1px solid #d8d8d8;
  margin-right: 13px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
  float: left;
}
.refunds-explain textarea{
  border: 1px solid #d8d8d8;
  width:400px;
  height: 90px;
  line-height: 28px;
  padding: 0 10px;
  resize:none;
  font-size: 14px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.refunds-reason select{
  border: 1px solid #d8d8d8;
  width:220px;
  height: 28px;
  line-height: 28px;
  padding: 0 10px;
  font-size: 14px;
}
.refunds-price span{
  margin-left: 10px;
}
.refunds-price b{
  font-weight: 100;
}
.refunds-price input{
  font-size: 14px;
  border: 1px solid #d8d8d8;
  width:200px;
  height: 28px;
  line-height: 28px;
  padding: 0 10px;
}
.refunds-number{
  border: 1px solid #d8d8d8;
  width:250px;
  height: 28px;
  line-height: 28px;
  padding: 0 10px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{
  -webkit-appearance: none !important;
  margin: 0; 
}
input[type="number"]{
  -moz-appearance:textfield;
}

.refunds-number input{
  font-size: 14px;
}
.refunds-retreat span{
  display: inline-block;
  margin-right:30px;
  width:90px;
  height: 28px;
  /*border: 1px solid #d8d8d8;*/
  text-align: center;
  line-height: 28px;
  /*cursor: pointer;*/
}
.refunds dd>p{
  margin-left: 100px;
}
.refunds dd>i{
  position: absolute;
  left: 20px;
  top: 5px;
}
.refunds dd{
  position: relative;
  margin-bottom: 20px;
}
.refunds{
  float: left;
  width: 1019px;
  height:700px;
  overflow: hidden;
  padding: 0 15px 30px 15px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  background: #fff;
  font-size: 14px;
}
.refunds-title{
  line-height: 60px;
  border-bottom: 1px solid #d8d8d8;
  font-size: 16px;
  margin-bottom: 30px;
}
</style>



