<template>
	<div class="w-help-menu">
		<p @click="goAnimate(0,115)"><i class="iconfont">&#xe60c;</i></p>
		<ul>
			<router-link tag="li" to="/help/origin"></router-link>
			<router-link tag="li" to="/help/quality"></router-link>
			<router-link tag="li" to="/help/delivery"></router-link>
			<router-link tag="li" to="/help/bestservice"></router-link>
		</ul>
		<p @click="goAnimate(1,115)"><i class="iconfont">&#xe60c;</i></p>
		<ul>
			<router-link tag="li" to="/help/registercertify"></router-link>
			<router-link tag="li" to="/help/userrights"></router-link>
			<router-link tag="li" to="/help/platformrules"></router-link>
			<router-link tag="li" to="/help/termsofservice"></router-link>
		</ul>
		<p @click="goAnimate(2,136)"><i class="iconfont">&#xe60c;</i></p>
		<ul>
			<router-link tag="li" to="/help/pledge"></router-link>
			<router-link tag="li" to="/help/returnterms"></router-link>
			<router-link tag="li" to="/help/returnprocess"></router-link>
			<router-link tag="li" to="/help/invoice"></router-link>
			<router-link tag="li" to="/help/problem"></router-link>
		</ul>
		<p @click="goAnimate(3,57)"><i class="iconfont">&#xe60c;</i></p>
		<ul>
			<router-link tag="li" to="/help/weare"></router-link>
			<router-link tag="li" to="/help/contactus"></router-link>
		</ul>
	</div>
</template>

<script>
  export default{
  	name:'help_menu',
  	data () {
  	  return{
  	  	msg:''
  	  }
  	},

  	methods:{
  		//
  		goAnimate(count,hCount){
  			if($(".w-help-menu ul").eq(count).height() > 10){
  				$(".w-help-menu ul").eq(count).animate({
  					height:0
  				},500,function(){
  					$(".w-help-menu p i").eq(count).html("&#xe60b;")
  				})
  			}else{
  				$(".w-help-menu ul").eq(count).animate({
  					height:hCount
  				},500,function(){
  					$(".w-help-menu p i").eq(count).html("&#xe60c;")
  				})
  			}
  		}
  	}
  }
</script>

<style scoped>
	.w-help-menu{
		width:100%;
		height:800px;
		background-color:#fff;
		padding:0 5px;
	}
	.w-help-menu p{
		line-height:52px;
		font-weight: 800;
		color:#333;
		cursor:pointer;
	}
	.w-help-menu p i{
		font-weight: 100;
		margin:0 10px 0 20px;
		font-size:14px;
	}
	.w-help-menu ul{
		overflow: hidden;
		
		padding-bottom:5px;
		border-bottom: 1px dashed #ccc;
	}
	.w-help-menu ul:last-of-type{
		border:none;
	}
	.w-help-menu ul li{
		cursor:pointer;
		padding-left:44px;
		font-size:14px;
		color:#333;
		line-height:26px;
	}
	.router-link-active{
		color:#009e4d !important;
	}
</style>


